<template>
    <div class="index-wrap">
      <div class="want-play-wrap">
         <span class="title">我也要玩</span>
         <input type="file" accept="image/*"  class="btn"  @change="uploadInit" ref="upload">
      </div>
      <transition name="hint"><div class="hint" v-show="showHint">照片不符合噢！</div></transition>
    </div>
</template>

<script>
    export default {
      data(){
        return {
          isSuc:false,
          imgSrc:'',
          showHint:false
        }
      },
      methods:{
        uploadInit(){
          let uploadInupt=this.$refs.upload
          let windowURL = window.URL || window.webkitURL;
          let dataURL;
          let _this=this
          // this.imgSrc=this.$refs.upload.value
          if (uploadInupt && uploadInupt.files && uploadInupt.files[0]) {
            dataURL = windowURL.createObjectURL(uploadInupt.files[0]);
            this.imgSrc=dataURL;
            uploadInupt.value='';
          }
          if(!this.isSuc){
            this.showHint=true;
            setTimeout(function () {
              _this.showHint=false;
            },2000)
          }
        }
      },
      created(){
      }

    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .index-wrap{
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
  }
  .want-play-wrap{
    position: relative;
    margin: 500px auto 0;
    width: 100px;
    height: 30px;
    line-height: 30px;
    background: #ff8930;
    color: #fff;
    .title{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    .btn{
      opacity: 0;
    }
  }
  .hint{
    position: absolute;
    width: 80%;
    height: 40px;
    left: 50%;
    top: 50%;
    background: rgba(0,0,0,0.5);
    color: #fff;
    text-align: center;
    line-height: 40px;
    font-size: 16px;
    border-radius: 10px;
    transform: translate(-50%,-50%);
  }
  .hint-enter-active, .hint-leave-active{
    opacity: 1;
    transition: all 3s
  }
  .hint-enter, .hint-leave-to{
    opacity: 0;
  }
</style>



